    See the JupyterLab Developer Guide for useful CSS Patterns:

    https://jupyterlab.readthedocs.io/en/stable/developer/css.html
*/

div:has(> .data-mount-dialog-body) {
  overflow: visible;
}

.data-mount-dialog-body {
  max-height: 80%;
}

.data-mount-dialog-config-header {
  font-weight: bold;
  text-align: center;
  margin-top: var(--bs-gutter-x) !important;
}

.data-mount-dialog-textfield {
  font-size: small !important;
}

.invalid {
  border: 1px solid red !important;
}

.data-mount-dialog-options {
  overflow-y: scroll;
  padding-right: 1em;
}

.icon-button {
  display: flex;
  align-items: center; /* Vertically center */
  justify-content: center; /* Horizontally center */
  width: 32px; /* Adjust button size */
  height: 32px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  border-radius: 5px;
  transition: background 0.2s ease-in-out;
}

.icon-button:hover {
  background: rgba(0, 0, 0, 0.1);
}

.icon-button-container {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.icon-button span {
  display: flex;
  align-items: center;
  justify-content: center;
}

.data-mount-sidebar {
  width: 100%;
  font-family: Arial, sans-serif;
}

.data-mount-sidebar-header {
  display: flex;
  background: #f5f5f5;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.header-item {
  flex: 1;
  font-weight: bold;
}

.header-item.name {
  text-align: left;
  padding-left: 10px;
}

.header-item.actions {
  text-align: right;
  padding-right: 10px;
}

.data-mount-sidebar-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.data-mount-sidepanel-header {
  text-align: center;
  padding: 0px !important;
  position: relative;
}


.data-mount-sidepanel-header-documentation {
  font-size: small;
  position: absolute;
  bottom: 0;
  right: 0;
  text-align: end;
}

.data-mount-sidebar-item {
  display: flex;
  align-items: center;
  padding: 8px 10px;
  border-bottom: 1px solid #ddd;
  transition: background 0.2s;
}

.data-mount-sidebar-item:hover {
  background: #f9f9f9;
}

.item-name {
  font-size: small;
  flex: 1;
  text-align: left;
}

.item-name.external {
  font-style: italic;
}

.item-actions {
  display: flex;
  gap: 8px;
}

.icon-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
}

.icon-button:hover {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.unmount {
  color: red;
}
